<template>
  <div class="page">
    <wv-group title="">
      <wv-input label="label" placeholder="" v-model="valueText"></wv-input>
      <wv-input label="label" placeholder="" type="number" v-model="valueNumber"></wv-input>
    </wv-group>

    <wv-group title="">
      <wv-input label=" abc" placeholder=" abc" v-model="valueText" pattern="^abc$"></wv-input>
      <wv-input label="" placeholder="" type="number" v-model="valueNumber" required></wv-input>
    </wv-group>

    <wv-group title="">
      <wv-input label="label" placeholder="" :readonly="true" v-model="valueReadonly"></wv-input>
    </wv-group>

    <wv-group title="">
      <wv-input label="" :labelWidth="190" placeholder="" v-model="valueText"></wv-input>
    </wv-group>

    <wv-group title="">
      <wv-input placeholder="" v-model="valueText"></wv-input>
    </wv-group>

    <wv-group title="">
      <wv-input label="" placeholder="" v-model="captcha">
        <button class="weui-vcode-btn" slot="ft"></button >
      </wv-input>
      <wv-input label="" class="weui-cell_vcode" placeholder="" v-model="vcode">
        <img :src="vcodeImage" class="vcode" slot="ft"/>
      </wv-input>
    </wv-group>
  </div>
</template>

<script type="text/babel">
import vcodeImage from '../assets/images/vcode.jpg'

export default {
  data () {
    return {
      valueText: 'hello we-vue',
      valueNumber: '',
      valueReadonly: '',
      captcha: '',
      vcode: '',
      vcodeImage
    }
  }
}
</script>



