<template>
  <div class="page">
    <wv-group title="">
      <wv-cell title="" :value="true"></wv-cell>
    </wv-group>

    <wv-group title="">
      <wv-cell title="">
        <img :src="imgIcon" slot="icon" class="cell-icon">
        <span slot="ft"></span>
      </wv-cell>
      <wv-cell title="" value="">
        <img :src="imgIcon" slot="icon" class="cell-icon">
      </wv-cell>
    </wv-group>

    <wv-group title="">
      <wv-cell title="" is-link to="/"></wv-cell>
      <wv-cell title="" is-link to="/"></wv-cell>
    </wv-group>

    <wv-group title="">
      <wv-cell title="" value="" is-link to="/"></wv-cell>
      <wv-cell title="" value="" is-link to="/"></wv-cell>
    </wv-group>

    <wv-group title="">
      <wv-cell title="" value="" is-link to="/">
        <img :src="imgIcon" alt="" slot="icon" class="cell-icon">
      </wv-cell>
      <wv-cell title="" value="" is-link to="/">
        <img :src="imgIcon" alt="" slot="icon" class="cell-icon">
      </wv-cell>
    </wv-group>
  </div>
</template>

<script type="text/babel">
  import imgIcon from '../assets/images/icon_tabbar.png'

  export default {
    data () {
      return {
        imgIcon
      }
    }
  }
</script>

<style scoped lang="scss">
  .cell-icon {
    display: block;
    width: 20px;
    margin-right: 5px;
  }
</style>



