<template>
  <div class="page-with-padding">
    <wv-button type="default" @click.native="popupVisible1 = true"></wv-button>
    <wv-button type="default" @click.native="popupVisible1 = true; height = 300"> 300px</wv-button>
    <wv-button type="default" @click.native="popupVisible2 = true"></wv-button>
    <wv-button type="default" @click.native="popupVisible3 = true"></wv-button>

    <wv-popup v-model="popupVisible1" @show="onShow" @hide="onHide" :height="height" background-color="white">
      <wv-group>
        <wv-switch title="" v-model="popupVisible1"></wv-switch>
        <wv-cell title="title" value="value" is-link></wv-cell>
        <wv-cell title="title" value="value" is-link></wv-cell>
      </wv-group>
    </wv-popup>

    <wv-popup v-model="popupVisible2" :hide-on-mask="false" @show="onShow" @hide="onHide">
      <wv-group>
        <wv-switch title="" v-model="popupVisible2"></wv-switch>
        <wv-cell title="title" value="value" is-link></wv-cell>
        <wv-cell title="title" value="value" is-link></wv-cell>
      </wv-group>
    </wv-popup>

    <wv-popup v-model="popupVisible3" mask-background-color="rgba(0, 255, 255, 0.5)" @show="onShow" @hide="onHide">
      <wv-group>
        <wv-switch title="" v-model="popupVisible3"></wv-switch>
        <wv-cell title="title" value="value" is-link></wv-cell>
        <wv-cell title="title" value="value" is-link></wv-cell>
      </wv-group>
    </wv-popup>
  </div>
</template>

<script type="text/babel">
export default {
  data () {
    return {
      height: 'auto',
      popupVisible1: false,
      popupVisible2: false,
      popupVisible3: false
    }
  },

  methods: {
    onShow () {
      console.log('shown')
    },

    onHide () {
      this.height = 'auto'
      console.log('hidden')
    }
  }
}
</script>



