<template>
  <div class="page">
    <wv-group>
      <wv-cell title="" :value="pickerValue1" is-link @click.native="pickerVisible1 = true"></wv-cell>
      <wv-cell title="" :value="pickerValue1" is-link @click.native="pickerVisible1 = true"></wv-cell>
      <wv-cell title="" :value="pickerValue1" is-link @click.native="pickerVisible2 = true"></wv-cell>
    </wv-group>

    <wv-datetime-picker v-model="pickerVisible1" :slots="slots1"></wv-datetime-picker>
  </div>
</template>

<script type="text/babel">
  export default {
    data () {
      return {
        pickerValue1: '',
        pickerVisible1: false,
        slots1: [
          {
            values: [
              '',
              '',
              '',
              '',
              ''
            ],
            defaultIndex: 2
          }
        ]
      }
    },

    methods: {
    }
  }
</script>

<style scoped lang="scss">
</style>



